.header{
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  background-color: rgba(44,44,44,255);
}



.nav{
  height: calc(var(--header-height)+1.5rem);
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 1rem;

}

.nav__logo,
.nav__toggle {
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
  font-size: var(--h2-font-size);
}

.nav__list {
  display: flex;
  flex-direction: row;
  column-gap: 2rem;
}
.nav__icon,
.nav__close,
.nav__toggle {
  display: none;
}

.nav__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: var(--large-font-size);
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
  transition: .3s;
}
.active-link,
.nav__link:hover{
  color: blueviolet;
}

/* Responsive typography */


@media screen and (max-width: 768px) {
  .header {
      top: initial;
      bottom: 0;
  }

  .nav {
      height: var(--header-height)
  }
  .nav__list {
      display: grid;
      grid-template-columns: repeat(3,1fr);
      gap: 2rem;
  }

  .nav__menu{
      position: fixed;
      bottom: -100%;
      left: 0;
      width: 100%;
      background-color: var(--body-color);
      padding: 2rem 1.5rem 4rem;
      box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
      border-radius: 1.5rem 1.5rem 0 0;
      transition: .3s;
  }

  .show-menu{
      bottom: 0;
  }
  
  .nav__icon {
  font-size: 1.2rem;
  }
  .nav__close {
      position: absolute;
      right: 1.3rem;
      bottom: .5rem;
      font-size: 1.5rem;
      cursor: pointer;
      color: var(--title-color)
  }

  .nav__close:hover {
      color: #c03
  }

  .nav__toggle{
      font-size: 1.1rem;
      cursor: pointer;
  }

  .nav__icon,
  .nav__close,
  .nav__toggle {
      display:block;
  }
}

@media screen and (max-width: 350px) {
  .nav__menu {
      padding: 2rem 0.25rem 4rem;
  }

  .nav__list {
      column-gap: 0;
  }

}